<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process269"></a>

<p><br></p>
<p><span class="rvts17">放假了（</span><span class="rvts50">School's Out</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:410,Y:235</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：贝利尔</span><span class="rvts20"></span><span class="rvts21">格里夫（</span><span class="rvts20">Beryl Griff</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助贝利尔说服斯括瑞（</span><span class="rvts20">Schori</span><span class="rvts21">）离开学校</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1485.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">找到贝利尔，与她交谈。当你提及在欢乐堡见到她的丈夫格里夫时，她会感到异常欣慰。她想要你把隔壁房间的斯括瑞从学校中赶走。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1486.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在学校后门，挪开木桶和箱子，可以进入学校。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1487.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在学校中可以遇到一个小女孩叫做崔娜（</span><span class="rvts20">Trina</span><span class="rvts21">），尝试说服崔娜，她会告诉你偷走林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室中的那扇屏风的小偷位置。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1488.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">通过一段对话，劝说贝利尔离开学校。之后再次找到贝利尔领赏。领赏的内容会根据对话选择不同而不同：</span>
<br><span class="rvts20">1. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">获得报答</span><span class="rvts20"></span><span class="rvts21">，她会给你一些符文配方</span>
<br><span class="rvts20">2. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">获得信息</span><span class="rvts20"></span><span class="rvts21">，她会告诉你林德凯姆洋馆的一些秘密。</span>
<br><span class="rvts20">3. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">不必感谢</span><span class="rvts20"></span><span class="rvts21">，她不会给你任何额外东西。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



